import React, { Component } from 'react';
import { Tooltip, OverlayTrigger } from 'react-bootstrap';
import { getRoiData, updateRoiData } from '../../../../Services/Actions/homeActions';
import { debounce } from '../../../../Services/Utils/utils';
import { updateUserInfo } from '../../../../Services/Actions/loginActions';

class UgcROI extends Component {
    state = {
        roiValue: 10,
        mediaValue: 0,
        roiData: { ROIValue: 0, approved: 0, collected: 0 },
        roiDataRequest: true,
    }

    componentDidMount() {
        this.onLoadROIData()
    }

    onLoadROIData = () => {

        var self = this;
        this.setState({ roiDataRequest: true })
        getRoiData().then((response) => {
            const { responseData } = response.data;
            if (responseData && Object.keys(responseData).length > 0) {
                self.setState({ roiDataRequest: false, roiData: responseData })
            }
            else {
                self.setState({ roiDataRequest: false })
            }
        })
            .catch((ex) => {
                self.setState({ roiDataRequest: false })
            })
    }

    roiCalculate = (event) => {
        const regex = /^[0-9\b]+$/;
        if (event.target.value === '' || regex.test(event.target.value) && event.target.value.length < 4) {
            let { roiData } = this.state
            roiData.ROIValue = event.target.value
            this.setState({
                roiData,
                roiValue: event.target.value,
                mediaValue: 23 * 132
            });
        }
    }

    setRoiData = debounce((event) => {
        var self = this
        const regex = /^[0-9\b]+$/;
        if (event.target.value === '' || regex.test(event.target.value) && event.target.value.length < 4) {
            updateRoiData({ ROIValue: event.target.value }).then((response) => {
                const { responseData } = response.data;
                updateUserInfo(self.props.dispatch)
            })
        }
    });

    onUpdateROI = event => {
        this.setRoiData(event)
    }

    onROIinputFocus = (event) => event.target.select();

    render() {
        const { roiData, roiDataRequest } = this.state;
        return <div className="card card-custom card-stretch gutter-b text-decoration-none">
            <div className="card-header pt-3 border-0">
                <h5 className="text-gray-dark fs-6 mb-0">UGC ROI Calculator</h5>
            </div>
            <div className="card-body d-flex flex-column pb-0">
                <span className="fs-7 fw-semibold text-ellipsis mb-2 text-gray-dark">Collected Assets</span>
                <div className="row gx-3">
                    <div className="col-sm">
                        <div className="card card-custom card-stretch gutter-b">
                            <div className="card-body d-flex flex-column roi_cal">
                                <div className="flex-grow-1">
                                    <span className="fw-semibold text-gray-500 mb-2 d-flex">Estimated Value
                                        <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Average cost of branded content creation.</Tooltip>}>
                                            <i className="ri-information-fill icon-6 ms-1 fw-normal"></i>
                                        </OverlayTrigger>
                                    </span>
                                    <div className="d-flex align-items-center justify-content-between">
                                        <div className="d-flex align-items-center">
                                            <div className="text-gray-dark fw-bold h5 me-1 mb-0">$</div>
                                            <input type="text" className="form-control form-control-sm fs-5 py-0 text-gray-dark fw-bold" style={{ maxWidth: 100 }} onChange={this.roiCalculate} onKeyUp={this.onUpdateROI} onFocus={this.onROIinputFocus} value={roiData.ROIValue} />
                                        </div>
                                        <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">
                                            <span className="svg-icon text-gray-500 me-0">
                                                <i className="ri-close-fill"></i>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm">
                        <div className="card card-custom card-stretch gutter-b">
                            <div className="card-body d-flex flex-column roi_cal">
                                <div className="flex-grow-1">
                                    <span className="fw-semibold text-gray-500 mb-2 d-flex">Total Assets
                                        <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">The total number of visual posts (@,#) that have been collected in your account.</Tooltip>}>
                                            <i className="ri-information-fill icon-6 ms-1 fw-normal"></i>
                                        </OverlayTrigger>
                                    </span>
                                    <div className="d-flex align-items-center justify-content-between">
                                        <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">{roiData.collected}</div>
                                        <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">
                                            <span className="svg-icon text-gray-500 me-0">
                                                <i className="ri-equal-fill"></i>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm">
                        <div className="card card-custom card-stretch gutter-b">
                            <div className="card-body d-flex flex-column pe-1">
                                <div className="flex-grow-1">
                                    <span className="fw-semibold text-gray-500 mb-2 d-flex">Potential Media Value
                                        <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">The total estimated value of your collected assets.</Tooltip>}>
                                            <i className="ri-information-fill icon-6 ms-1 fw-normal"></i>
                                        </OverlayTrigger>
                                    </span>
                                    <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">${roiData.ROIValue * roiData.collected}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <span className="fs-7 fw-semibold text-ellipsis mb-2 text-gray-dark">Approved Assets</span>
                <div className="row gx-3">
                    <div className="col-sm">
                        <div className="card card-custom card-stretch gutter-b">
                            <div className="card-body d-flex flex-column">
                                <div className="flex-grow-1">
                                    <span className="fw-semibold text-gray-500 mb-2 d-flex">Total Assets
                                        <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">The total number of visual posts (@,#) that have been collected in your account.</Tooltip>}>
                                            <i className="ri-information-fill icon-6 ms-1 fw-normal"></i>
                                        </OverlayTrigger>
                                    </span>
                                    <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">{roiData.collected}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm">
                        <div className="card card-custom card-stretch gutter-b">
                            <div className="card-body d-flex flex-column">
                                <div className="flex-grow-1">
                                    <span className="fw-semibold text-gray-500 mb-2 d-flex">Approved Media
                                        <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Total visuals with approved user's rights.</Tooltip>}>
                                            <i className="ri-information-fill icon-6 ms-1 fw-normal"></i>
                                        </OverlayTrigger>
                                    </span>
                                    <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">{roiData.approved}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm">
                        <div className="card card-custom card-stretch gutter-b">
                            <div className="card-body d-flex flex-column">
                                <div className="flex-grow-1">
                                    <span className="fw-semibold text-gray-500 mb-2 d-flex">Content Value
                                        <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">The total value of assets that have been granted user rights.</Tooltip>}>
                                            <i className="ri-information-fill icon-6 ms-1 fw-normal"></i>
                                        </OverlayTrigger>
                                    </span>
                                    <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">${roiData.approved * roiData.ROIValue}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    }
}

export default UgcROI